.picture-container {
  position: relative;
  color: white;
  padding-bottom: 5rem;
}

.picture-container:hover .visibility-hidden :nth-child(n) {
  visibility: visible;
  opacity: 1;
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-name: fadeInBottom;
}

.picture {
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  object-fit: none;
  object-position: 0 0;
}

@media screen and (min-width: 1580px) {
  .picture {
    height: 50rem;
    width: 45rem;
  }
}

@media screen and (max-width: 1579px) {
  .picture {
    height: 50rem;
    width: 35rem;
  }
}

.picture-container:hover .picture {
  transition: -webkit-filter 300ms linear;
  -webkit-transition: -webkit-filter 300ms linear;
  filter: brightness(10%);
  -webkit-filter: brightness(10%);
}

.text {
  position: relative;
  bottom: 48rem;
  left: 4rem;
  font-size: 4rem;
  font-weight: 500;
  height: 2rem;
}

.experience-text {
  position: absolute;
  top: 8rem;
  left: 4rem;
}

.text-on-hover:first-child {
  top: -42rem;
}

.text-on-hover {
  margin-top: 1rem;
  margin-right: 4rem;
}

.visibility-hidden {
  visibility: hidden;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.github-link {
  color: #fff;
  text-decoration: none;
}

.github-link:hover {
  text-decoration: underline;
  text-underline-offset: 2px;
}

.skill-rating {
  margin-top: 5rem;
}